<template lang="pug">
  .list-dish-category-wrapper
    .item(v-for='dish in d_listDish')
      md-dish(:id="dish.id", :image="dish.imageUrl", :title="dish.title", :price="dish.price", @delDish="delDish")
    md-addnewdish





</template>

<script>
  export default {
    name: 'md-listdish',
    props: {
      listDish: {
        type: Array
      }
    },
    data() {
      return {
        d_listDish: []
      }
    },
    watch: {
      listDish: function (val) {
        this.d_listDish = val
      }
    },
    methods: {
      delDish(id) {
        var self = this
        var newLish = self.d_listDish.filter((dish) => {
          return dish.id !== id
        })
        if (newLish) {
          this.d_listDish = newLish
        }
      }
    }

  }

</script>

<style lang="scss"></style>



