<template lang="pug">
.content
  .md-button.btn.btn-primary.pull-right(@click='newCategory') Thm Category
  .row
    .col-md-12(v-for='list in list_category')
      md-card(:title='list.title', :category='list.sub_title', backgroundColor='purple')
        md-listdish(:listDish="listDish")


</template>

<script>

  import axios from 'axios'

  export default {
    data() {
      return {
        list_category: [
          {
            title: 'Mn n nc',
            sub_title: 'y l cc mn n nc, h tr tt cho vic tiu ha'
          }
        ],
        listDish: [
          {
            image: '/static/images/dish/1 (1).jpg',
            title: 'Mn n',
            date: 'May 11, 2018'
          }
        ],
        data_test: '',
        vaicalua: ''
      }
    },
    methods: {
      newCategory() {
        alert('Your data:')
      },
      getListDish() {
        var token = window.localStorage.getItem('user_token')
        axios
          .get('http://18.233.9.137/admin/dish/list/?offset=0&pageNumber=0&pageSize=10', {
            headers: {Authorization: token}
          })
          .then(response => (
            this.listDish = response.data.content
          ))
      }
    },
    created() {
      this.getListDish()
    }
  }
</script>
<style lang="sass">

</style>



