<template lang="pug">
  .list-dish
    img.img(:src='image')
    .content-wrapper
      .title
        .p-title
          span {{title}}
        .date-cre
          span Gi: {{price}}
      .button-wrapper
          i.material-icons(@click="editDish") edit
          i.material-icons(@click="delDish") delete


</template>

<script>
  import swal from 'sweetalert'
  import axios from 'axios'

  export default {
    name: 'md-dish',
    props: {
      id: {
        type: Number
      },
      image: {
        type: String
      },
      title: {
        type: String
      },
      date: {
        type: String
      },
      price: {
        type: Number
      }
    },
    methods: {
      editDish() {
        this.$router.push('Menu/editDish/' + this.id)
      },
      delDish() {
        var self = this
        var token = window.localStorage.getItem('user_token')
        swal({
          title: 'Cnh bo?',
          text: 'Mn n  xa s khng khi phc d liu li c!',
          icon: 'warning',
          buttons: [
            'Khng',
            'C'
          ],
          dangerMode: true
        })
          .then(willDelete => {
            if (willDelete) {
              axios.delete('/admin/dish/delete/' + self.id.toString(), {
                headers: {Authorization: token}
              })
                .then(function (response) {
                  self.$emit('delDish', self.id)
                  swal(' xa!', ' xa mn n thnh cng', 'success')
                })
            }
          })
      }
    },
    filters: {
      dateTimeFormat: function (value) {
        var today = new Date(value)
        var dd = today.getDate()
        var mm = today.getMonth() + 1

        var yyyy = today.getFullYear()
        if (dd < 10) {
          dd = '0' + dd
        }
        if (mm < 10) {
          mm = '0' + mm
        }
        var todayr = dd + '/' + mm + '/' + yyyy
        return todayr
      }
    }
  }
</script>

<style lang="scss"></style>



